'use client';

import { useState, useEffect } from 'react';
import { Card } from '@/components/common';

interface DebugInfo {
  environment: string;
  nodeVersion: string;
  userAgent: string;
  viewport: { width: number; height: number };
  timestamp: string;
}

export default function DebugPanel() {
  const [debugInfo, setDebugInfo] = useState<DebugInfo | null>(null);
  const [isVisible, setIsVisible] = useState(false);

  useEffect(() => {
    if (typeof window !== 'undefined') {
      setDebugInfo({
        environment: process.env.NODE_ENV || 'development',
        nodeVersion: process.version || 'unknown',
        userAgent: navigator.userAgent,
        viewport: {
          width: window.innerWidth,
          height: window.innerHeight
        },
        timestamp: new Date().toISOString()
      });
    }
  }, []);

  if (!debugInfo) return null;

  return (
    <>
      {/* Debug Toggle Button */}
      <button
        onClick={() => setIsVisible(!isVisible)}
        className="fixed bottom-4 right-4 bg-gray-800 text-white p-2 rounded-full shadow-lg hover:bg-gray-700 z-50"
        aria-label="Toggle debug panel"
      >
        🐛
      </button>

      {/* Debug Panel */}
      {isVisible && (
        <div className="fixed bottom-16 right-4 w-80 max-h-96 overflow-auto z-50">
          <Card className="bg-gray-900 text-white">
            <div className="flex justify-between items-center mb-4">
              <h3 className="text-lg font-bold">Debug Info</h3>
              <button
                onClick={() => setIsVisible(false)}
                className="text-gray-400 hover:text-white"
                aria-label="Close debug panel"
              >
                ✕
              </button>
            </div>
            
            <div className="space-y-3 text-sm">
              <div>
                <strong>Environment:</strong> {debugInfo.environment}
              </div>
              <div>
                <strong>Viewport:</strong> {debugInfo.viewport.width} × {debugInfo.viewport.height}
              </div>
              <div>
                <strong>Timestamp:</strong> {new Date(debugInfo.timestamp).toLocaleString()}
              </div>
              <div>
                <strong>User Agent:</strong>
                <div className="mt-1 text-xs text-gray-300 break-all">
                  {debugInfo.userAgent}
                </div>
              </div>
              
              {/* API Health Check */}
              <div className="border-t border-gray-700 pt-3">
                <strong>API Status:</strong>
                <div className="mt-1">
                  <span className="inline-block w-2 h-2 bg-yellow-500 rounded-full mr-2"></span>
                  Backend: Not Connected (Mock Mode)
                </div>
              </div>
              
              {/* Local Storage Info */}
              <div className="border-t border-gray-700 pt-3">
                <strong>Storage:</strong>
                <div className="mt-1 text-xs">
                  <div>Auth Token: {localStorage.getItem('auth_token') ? '✓ Present' : '✗ Missing'}</div>
                  <div>User Data: {localStorage.getItem('user') ? '✓ Present' : '✗ Missing'}</div>
                </div>
              </div>
            </div>
          </Card>
        </div>
      )}
    </>
  );
}